.header_img {
  background: #000 url("../images/header-fon.jpg") center center no-repeat;
  min-height: 770px;
  background-size: cover;
  position: relative; }

.header_img:before {
  position: absolute;
  content: "";
  background: rgba(17, 17, 17, 0.5);
  width: 100%;
  height: 100%;
  bottom: 0;
  left: 0; }

.top_line {
  background: rgba(17, 17, 17, 0.5);
  background-color: transparent;
  position: relative;
  height: 100px;
  z-index: 100; }

.top_line .active {
  width: 100%;
  position: fixed; }

.top_line_box {
  display: flex;
  height: 100px;
  align-items: center;
  justify-content: space-between; }

.top_line_logo {
  /*  // font-size: $size; */
  font-size: 1.5rem;
  line-height: 1;
  text-transform: uppercase;
  font-family: "OpenSansSemiBold", sans-serif;
  color: #fff; }

.top_line_menu {
  padding: 0;
  margin: 0; }

.top_line_menu ul li {
  display: inline-block;
  padding: 0 10px; }

.top_line_menu ul li a {
  display: block;
  text-transform: uppercase;
  color: #fff;
  padding: 10px 0;
  /*  // font-size: $size; */
  font-size: 0.875rem;
  line-height: 1.71429;
  -moz-transition: all 0.2s ease;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
  position: relative; }

.top_line_menu ul li a:hover {
  color: #fcac45; }

.top_line_menu ul li a:hover:before {
  position: absolute;
  bottom: 0;
  left: 0;
  content: '';
  height: 2px;
  width: 100%;
  background-color: #fcac45; }

/*  оформление мобильной версии меню и гамбургера */
.mobile_gamburger {
  display: none; }

.mobile_gamburger {
  width: 50px;
  height: 50px;
  display: none;
  z-index: 100; }

.toggle-mnu {
  display: block;
  /*   float:right;
position:absolute;
right:10px;top:0; */
  text-align: center;
  line-height: 48px; }

.toggle-mnu {
  display: block;
  width: 50px;
  height: 50px;
  margin-top: 15px;
  margin-right: 0; }

.toggle-mnu span:after, .toggle-mnu span:before {
  content: "";
  position: absolute;
  left: 0;
  top: 9px; }

.toggle-mnu span:after {
  top: 18px; }

.toggle-mnu span {
  position: relative;
  display: block; }

.toggle-mnu span, .toggle-mnu span:after, .toggle-mnu span:before {
  width: 100%;
  height: 3px;
  background-color: #000;
  transition: all .3s;
  backface-visibility: hidden;
  border-radius: 3px; }

.toggle-mnu.on span {
  background-color: transparent; }

.toggle-mnu.on span:before {
  -webkit-transform: rotate(45deg) translate(-1px, 0);
  -ms-transform: rotate(45deg) translate(-1px, 0);
  transform: rotate(45deg) translate(-1px, 0); }

.toggle-mnu.on span:after {
  -webkit-transform: rotate(-45deg) translate(6px, -7px);
  -ms-transform: rotate(-45deg) translate(6px, -7px);
  transform: rotate(-45deg) translate(6px, -7px); }

.toggle-mnu.on + #menu {
  opacity: 1;
  visibility: visible; }

/*оформление скрытого меню*/
.hidden-mnu {
  background-color: rgba(0, 0, 0, 0.7);
  display: none;
  min-width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1; }

.hidden-mnu ul {
  margin: 0;
  padding: 0; }

.hidden-mnu ul li {
  display: block;
  margin: 0;
  padding: 0; }

.hidden-mnu ul li a {
  display: block;
  cursor: pointer;
  text-align: center;
  font-size: 1.6rem;
  text-transform: uppercase;
  text-decoration: none;
  line-height: 1.5;
  padding: 20px 0;
  color: #fff;
  -moz-transition: all 0.2s ease;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease; }

.hidden-mnu ul li a:hover {
  /* background-color:rgba(40, 40, 38, 0.60); */
  color: #FF8941; }

