*, *:before, *:after {
  box-sizing: border-box;
   outline:none;
   padding: 0;
  margin: 0;
}
@import '_fonts.scss';

html{
width: 100%;
/* height: 100%; */
font-size: 16px;
line-height: 1.42;
margin:0;
padding:0;
-webkit-backface-visibility:hidden;
/* overflow-x: hidden; */
}
body {
font-family: "OpenSansRegular", sans-serif;
font-size: 16px;
line-height: 1.71;
position: relative;
min-width: 320px;
height: 100%;
margin:0;
padding:0;
color: #000000;
opacity: 1;
-webkit-backface-visibility:hidden;
-webkit-font-smoothing:antialised;
overflow-x: hidden;
}

